@import url("https://fonts.googleapis.com/css?family=PT+Sans:400,700|Roboto:400,500&display=swap&subset=cyrillic");
@font-face {
  font-family: "MerriweatherItalic";
  src: url("../fonts/MerriweatherItalic/MerriweatherItalic.eot");
  src: url("../fonts/MerriweatherItalic/MerriweatherItalic.ttf");
  src: url("../fonts/MerriweatherItalic/MerriweatherItalic.woff"); }

html {
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;
  color: #333; }

body {
  background: #ccc;
  margin: 30px 50px;
  line-height: 1.4; }

h1, h2, h3, p {
  margin: 0;
  padding: 0; }

a {
  text-decoration: none;
  display: inline-block; }

ul, li {
  list-style-type: none;
  padding: 0;
  margin: 0; }

.btn {
  background: #333;
  color: #fff;
  padding: 9px 20px;
  text-decoration: none;
  border: 0; }
  .btn:hover {
    background: #969696;
    color: #333; }

img {
  max-width: 100%; }

.wrapper {
  display: grid;
  grid-gap: 20px; }

.main__nav ul {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 20px; }
  .main__nav ul a {
    background: #ddd;
    display: block;
    padding: 8px;
    text-align: center;
    color: #333;
    text-transform: uppercase;
    font-size: 18px;
    box-shadow: 0 1px 5px rgba(104, 104, 104, 0.8);
    transition: all .3s ease; }
    .main__nav ul a:hover {
      background: #333;
      color: #fff; }

.top__container {
  display: grid;
  grid-gap: 20px;
  grid-template-areas: 'showcase showcase top__box-a' 'showcase showcase top__box-b';
  grid-template-rows: repeat(2, auto); }

.showcase {
  grid-area: showcase;
  min-height: 400px;
  background: url(https://image.ibb.co/kYJK8x/showcase.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  padding: 48px;
  box-shadow: 0 1px 5px rgba(104, 104, 104, 0.8);
  display: flex;
  align-items: start;
  flex-direction: column;
  justify-content: center; }
  .showcase h1 {
    font-size: 50px;
    margin-bottom: 0;
    color: #fff; }
  .showcase p {
    font-size: 20px;
    margin-top: 0;
    color: #fff; }
  .showcase a {
    margin-top: 25px; }

.top__box {
  background: #ddd;
  display: grid;
  align-items: center;
  justify-items: center;
  padding: 24px;
  box-shadow: 0 1px 5px rgba(104, 104, 104, 0.8); }
  .top__box .price {
    font-size: 38px; }

.top__box-a {
  grid-area: top__box-a; }

.top__box-b {
  grid-area: top__box-b; }

.boxes {
  display: grid;
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr)); }

.box {
  background: #ddd;
  text-align: center;
  padding: 25px 32px;
  box-shadow: 0 1px 5px rgba(104, 104, 104, 0.8);
  cursor: pointer;
  transition: all .3s ease; }
  .box:hover {
    box-shadow: none; }

.info {
  background: #ddd;
  box-shadow: 0 1px 5px rgba(104, 104, 104, 0.8);
  display: grid;
  grid-gap: 30px;
  grid-template-columns: repeat(2, 1fr);
  padding: 48px; }
  .info p {
    margin-top: 20px; }
  .info a {
    margin-top: 15px; }

.portfolio {
  display: grid;
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fit, minmax(200px, auto)); }
  .portfolio img {
    width: 100%;
    box-shadow: 0 1px 5px rgba(104, 104, 104, 0.8); }

footer {
  margin-top: 32px;
  background: #333;
  text-align: center;
  padding: 16px;
  color: #fff; }

/*   less  1800px     */
/*   less  1130px     */
/*   less  992px     */
/*   less  700px     */
@media screen and (max-width: 700px) {
  .top__container {
    grid-template-areas: "showcase showcase" "top__box-a top__box-b"; }
  .showcase h1 {
    font-size: 38px; }
  .main__nav ul {
    grid-template-columns: 1fr; }
  .info {
    grid-template-columns: 1fr; }
    .info .btn {
      display: block;
      text-align: center;
      margin: auto; } }

/*   less  600px     */
/*   less  500px     */
@media screen and (max-width: 500px) {
  body {
    margin: 5px 10px; }
  .top__container {
    grid-template-areas: 'showcase' 'top__box-a' ' top__box-b'; } }

/*   less  420px     */
/*   min  768px     */
