@import "vars";
@import "fonts";
@import "mixin";
@import url('https://fonts.googleapis.com/css?family=PT+Sans:400,700|Roboto:400,500&display=swap&subset=cyrillic');


html {
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;
  color: $dark;
}

body {
  background: #ccc;
  margin: 30px 50px;
  line-height: 1.4;
}

h1, h2, h3, p {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  display: inline-block;
}

ul, li {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.btn {
  background: $dark;
  color: $light;
  padding: 9px 20px; 
  text-decoration: none;
  border: 0;

  &:hover {
    background: rgb(150, 150, 150);
    color: $dark;
  }
}

img {
  max-width: 100%;
}

.wrapper {
  display: grid;
  grid-gap: 20px;
}

.main__nav {

  ul {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 20px;

  
    a {
      background: $primary;
      display: block;
      padding: 8px;
      text-align: center;
      color: $dark;
      text-transform: uppercase;
      font-size: 18px;
      box-shadow: $shadow;
      transition: all .3s ease; 

      &:hover {
        background: $dark;
        color: $light;
      }
    }


  }

}

//  Top container
.top__container {
  display: grid;
  grid-gap: 20px;
  grid-template-areas: 
    'showcase showcase top__box-a'
    'showcase showcase top__box-b';
    grid-template-rows: repeat(2, auto);
}

.showcase {
  grid-area: showcase;
  min-height: 400px;
  background: url(https://image.ibb.co/kYJK8x/showcase.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  padding: 48px;
  box-shadow: $shadow;

  display: flex;
  align-items: start;
  flex-direction: column;
  justify-content: center;

  h1 {
    font-size: 50px;
    margin-bottom: 0;
    color: $light;
  }

  p {
    font-size: 20px;
    margin-top: 0;
    color: $light;
  }

  a {
    margin-top: 25px;
  }
}

//  Top  box
.top__box {
  background: $primary;
  display: grid;
  align-items: center;
  justify-items: center;
  padding: 24px;
  box-shadow: $shadow;

  .price {
    font-size: 38px;
  }
}

.top__box-a {
  grid-area: top__box-a;
}

.top__box-b {
  grid-area: top__box-b;
}

// Boxes
.boxes {
  display: grid;
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
}

.box {
  background: $primary;
  text-align: center;
  padding: 25px 32px;
  box-shadow: $shadow;
  cursor: pointer;
  transition: all .3s ease;

  &:hover {
    box-shadow: none;
  }
}


// Info
.info {
  background: $primary;
  box-shadow: $shadow;
  display: grid;
  grid-gap: 30px;
  grid-template-columns: repeat(2, 1fr);
  padding: 48px;

  p {
    margin-top: 20px;
  }

  a {
    margin-top: 15px;
  }
}

// Portfolio
.portfolio {
  display: grid;
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fit, minmax(200px, auto));

  img {
    width: 100%;
    box-shadow: $shadow;
  }
}

// Footer
footer {
  margin-top: 32px;
  background: $dark;
  text-align: center;
  padding: 16px;
  color: $light;
}

@import "./media.scss";